var React = require('react');
var _ = require('lodash');

var ChatContent = React.createClass({
    render: function(){
        let msgs = this.props.chatData;


        return (
            <div id="chatscroll">
                <ul id="chatbox">
                    {msgs}
                </ul>
            </div>
        )
    }

});

module.exports = ChatContent;


